// src/components/ProjectsSection.tsx
import React from "react";
import Link from "next/link";
import Image from "next/image";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowUpRightFromSquare, faCode } from '@fortawesome/free-solid-svg-icons';

// Define the structure for a single project
interface Project {
  title: string;
  description: string;
  image: string; 
  tags: string[];
  githubUrl: string;
  liveUrl: string;
}

// Sample Project Data (Ensure you have images at these paths!)
const PROJECTS: Project[] = [
  {
    title: "AI Recipe Generator",
    description: "A full-stack application leveraging OpenAI's API to generate unique recipes based on user-provided ingredients and dietary preferences.",
    image: "/images/ai-recipe.jpg", 
    tags: ["Next.js", "TypeScript", "Tailwind CSS", "OpenAI API", "MongoDB"],
    githubUrl: "https://github.com/yourusername/ai-recipe-generator",
    liveUrl: "https://ai-recipe.vercel.app/",
  },
  {
    title: "E-commerce Backend Service",
    description: "A robust RESTful API built with Node.js and Express, handling user authentication, order processing, and inventory management.",
    image: "/images/ecommerce-api.jpg", 
    tags: ["Node.js", "Express", "PostgreSQL", "Prisma", "AWS EC2"],
    githubUrl: "https://github.com/yourusername/ecommerce-backend",
    liveUrl: "#", 
  },
  {
    title: "Custom OS Monitoring Tool",
    description: "A desktop application written in Python that monitors system resources (CPU, Memory, Disk) and logs performance metrics locally.",
    image: "/images/os-monitor.jpg", 
    tags: ["Python", "Tkinter", "System Metrics", "Data Logging"],
    githubUrl: "https://github.com/yourusername/os-monitor-tool",
    liveUrl: "#", 
  },
];

const ProjectsSection: React.FC = () => {
  return (
    // Removed background color (default transparent/white)
    <section id="projects" className="py-20 md:py-32">
      <div className="max-w-5xl mx-auto px-6 lg:px-8"> 
        
        {/* Section Heading */}
        <h2 className="text-4xl md:text-5xl font-extrabold text-gray-800 text-center mb-4">
          My Projects
        </h2>
        <p className="text-xl text-gray-600 text-center mb-16 max-w-2xl mx-auto">
          A selection of recent projects showcasing my expertise.
        </p>

        {/* Projects Grid - Single column (vertically stacked) */}
        <div className="grid gap-10 sm:grid-cols-1">
          {PROJECTS.map((project, index) => (
            <ProjectCard key={index} project={project} />
          ))}
        </div>

      </div>
    </section>
  );
};

// Component for a single project card
const ProjectCard: React.FC<{ project: Project }> = ({ project }) => {
  return (
    // Enhanced Hover: scale-[1.03] and shadow-2xl
    <div 
      className="group flex flex-col md:flex-row rounded-xl overflow-hidden 
                 bg-white shadow-lg border border-gray-100 
                 transition-all duration-500 transform 
                 hover:scale-[1.03] hover:shadow-2xl"
    >
      {/* Project Image */}
      <div className="relative w-full md:w-1/2 h-56 md:h-auto overflow-hidden">
        <Image
          src={project.image}
          alt={`Image of ${project.title}`}
          fill 
          className="object-cover transition duration-500 group-hover:scale-110" // Aggressive zoom on image
          sizes="(max-width: 768px) 100vw, 50vw"
          priority={true}
        />
        <div className="absolute inset-0 bg-gray-900 bg-opacity-0 transition-opacity duration-300 group-hover:bg-opacity-10" />
      </div>

      {/* Project Content */}
      <div 
        className="p-8 w-full md:w-1/2 
                   transition-transform duration-500 
                   group-hover:translate-y-[-4px]" // Slight lift on content on hover
      >
        <h3 className="text-3xl font-bold text-gray-800 mb-3">{project.title}</h3>
        
        <div className="flex flex-wrap gap-2 mb-4">
          {project.tags.map((tag, i) => (
            <span 
              key={i} 
              className="px-3 py-1 text-xs font-medium text-blue-800 bg-blue-100 rounded-full"
            >
              {tag}
            </span>
          ))}
        </div>

        <p className="text-gray-600 mb-6 leading-relaxed">
          {project.description}
        </p>

        {/* Links */}
        <div className="flex gap-6">
          {project.githubUrl && (
            <Link 
              href={project.githubUrl} 
              target="_blank" 
              className="flex items-center gap-2 text-gray-500 hover:text-blue-600 font-semibold transition-colors duration-200"
            >
              <FontAwesomeIcon icon={faCode} className="w-5 h-5" />
              <span>View Code</span>
            </Link>
          )}
          
          {project.liveUrl !== '#' && (
            <Link 
              href={project.liveUrl} 
              target="_blank" 
              className="flex items-center gap-2 text-blue-600 hover:text-blue-800 font-semibold transition-colors duration-200"
            >
              <FontAwesomeIcon icon={faArrowUpRightFromSquare} className="w-5 h-5" />
              <span>Live Demo</span>
            </Link>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProjectsSection;